<template>
    <div class="main">
        <div class="districtAccount">
            <div class="searchA">
                <el-select v-model="region_id" v-show="!isCity" clearable placeholder="请选择区县" size='small' style="margin-right:20px;">
                    <el-option v-for="item in regionData" :key="item.id" :label="item.region_name" :value="item.id">
                    </el-option>
                </el-select>
                <div class="padd" style="margin-right:20px;">
                    <el-input v-model="keyword" placeholder="请输入关键字、拼音" clearable size="small"></el-input>
                </div>
                <div class="padd" style="margin-right:20px;">
                    <el-button size="small" type="primary"  plain icon="el-icon-search" @click="getList(1)">查询</el-button>
                </div>
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" :disabled="!method_auth.actAdd ? true : false" @click="addBtn()">新增</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" style="width: 100%" size="medium">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" v-if="item.title == '状态'" width="100">
                            <template slot-scope="scope">
                                <el-switch v-model="tableData[scope.$index][item.prop]" @change="
                                    switchChange(
                                    scope.row.id,
                                    item.prop,
                                    tableData[scope.$index][item.prop],
                                    scope.$index
                                    )
                                " :disabled="!method_auth.setState"></el-switch>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :min-width="item.minwidth" v-else :show-overflow-tooltip='true'></el-table-column>
                    </template>

                    <el-table-column prop="address" label="操作" fixed="right" width="150">
                        <template slot-scope="scope">
                            <el-button @click="editBtn(scope.row)" type="text" :disabled="!method_auth.actEdit ? true : false" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" type="text" :disabled="!method_auth.actDelete ? true : false" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="pagination">
                <el-pagination background :current-page.sync="curr" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes, total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
            </div>

            <!-- 新增编辑弹窗 -->
            <el-dialog class="dialogStyle" :title="title" v-dialogDrag :visible.sync="dialogVisible" width="600px" :close-on-click-modal="false" @close="handleClose('formData')">
                <div>
                    <el-form :model="formData" ref="formData" :rules="rule" class="demo-ruleForm">

                        <div class="dialogInput" style="display:flex">
                            <div class="padd" style="margin-right: 10px;">
                                <!-- <el-form-item label="用户名" prop="user_name">
                                    <el-input v-model="formData.user_name" placeholder="请输入用户名" clearable size="small"></el-input>
                                </el-form-item> -->

                                <el-form-item label="登录账号" prop="user_name">
                                    <el-input v-model="formData.user_name" placeholder="请输入登录手机号码" clearable size="small" maxlength="11"></el-input>
                                </el-form-item>

                            </div>

                            <div class="padd">
                                <el-form-item label="真实姓名" prop="real_name">
                                    <el-input v-model="formData.real_name" placeholder="请输入姓名" clearable size="small"></el-input>
                                </el-form-item>
                            </div>
                        </div>

                        <div class="dialogInput" style="display:flex">
                            <div class="padd" style="margin-right: 10px;">
                                <el-form-item label="手机号">
                                    <el-input v-model="formData.mobile" placeholder="微信/i襄阳注册手机号" clearable size="small"></el-input>
                                </el-form-item>
                            </div>

                            <div class="padd">
                                <el-form-item label="密码">
                                    <el-input type="password" v-model="formData.password" placeholder="请输入密码" clearable size="small"></el-input>
                                </el-form-item>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd" style="width:275px;">
                                <div class="paddTit">部门</div>
                                <div>
                                    <el-form-item prop="selectVal1">
                                        <el-select v-model="formData.selectVal1" style="width:100%" placeholder="请选择部门" size="small" @change='quvSelectChange(formData.selectVal1)'>
                                            <el-option v-for="item in departmentData" :key="item.id" :label="item.department_name" :value="item.id">
                                            </el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput" style="display:flex">
                            <div class="padd" style="margin-right: 10px;">
                                <el-form-item prop="main_account" label="负责人账号">
                                    <el-switch v-model="formData.main_account"></el-switch>
                                </el-form-item>
                            </div>

                            <div class="padd" style="margin-right: 10px;">
                                <el-form-item prop="disabled" label="状态">
                                    <el-switch v-model="formData.disabled" :disabled="!method_auth.setState"></el-switch>
                                </el-form-item>
                            </div>
                        </div>

                        <div class="dialogInput">
                            <div class="padd">
                                <div class="paddTit">功能节点</div>
                                <div class="tree_main">
                                    <el-form-item>
                                        <el-tree :data="nodeData" show-checkbox check-on-click-node node-key="id" ref="tree" default-expand-all :props="defaultProps">
                                        </el-tree>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div style="text-align: center;">
                            <el-button type="primary" class="btn" @click="submitForm('formData')">确 定</el-button>
                        </div>

                    </el-form>

                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    hashApi,
    areaManageListApi,
    areaManageActAddApi,
    areaManageActDelApi,
    areaManageViewApi,
    areaManageActEditApi,
    areaManageInfoApi,
    areaManageSetApi,
} from "@/api/index";
export default {
    data() {
        return {
            formData: {
                user_name: "",
                real_name: "",
                password: "",
                mobile: "",
                department_id: "",
                selectVal1: "",
                main_account: true, // 是否主账号
                disabled: true // 启动0，禁用1
            },
            method_auth: {
                actAdd: 0,
                actEdit: 0,
                actDelete: 0,
                setState: 0,
            },
            main_account: true,
            disabled: true,
            isCity: 0,
            main_auth: 0,
            tableHeight: window.innerHeight - 180,
            listId: "",
            total: 0,
            curr: 1,
            region_id: "",
            keyword: "",
            hash: "",
            pageSize: 15,
            departmentData: [],
            regionData: [],
            title: "",
            isLoading: false,
            dialogVisible: false,
            switchVal: true,
            rule: {
                user_name: [
                    { required: true, message: '请输入登录手机号码', trigger: 'blur' },
                    {
                        required: true,
                        pattern: /^1\d{10}$/,
                        message: '手机号格式错误',
                        trigger: 'blur'
                    }
                ],
                real_name: [
                    { required: true, message: '请输入姓名', trigger: 'blur' },
                    { min: 2, max: 24, message: '姓名长度在 2 到 24 个字符', trigger: 'blur' },
                    {
                        required: true,
                        pattern: /^[\u4e00-\u9fa5_a-zA-Z.·]+$/,
                        message: '姓名不支持特殊字符',
                        trigger: 'blur'
                    }
                ],

                selectVal1: [
                    { required: true, message: '请选择部门', trigger: 'change' },
                ],
            },
            tableHead: [
                { title: "编号", prop: "id", width: '100' },
                { title: "部门", prop: "department_name", minwidth: '180' },
                { title: "登录账号", prop: "user_name", minwidth: '150' },
                { title: "姓名", prop: "real_name", minwidth: '100' },
                { title: "手机号", prop: "mobile", minwidth: '150' },
                { title: "状态", prop: "disabled" },
            ],

            tableData: [],
            nodeData: [],
            infoData: [],
            hasNodes: [],
            defaultProps: {
                children: 'children',
                label: 'node_name',
                disabled: 'defaulted',
            },

        };
    },
    mounted() {
        this.getList();
        this.getNodeData();
        this.getHash();
    },
    methods: {
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.curr = 1;
            this.pageSize = newSize;
            this.getList()
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.curr = newPage;
            this.getList()
        },

        // 新增编辑提交
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    if (this.formData.main_account == true) {
                        var main_account = 1;
                    } else {
                        var main_account = 0;
                    }
                    if (this.formData.disabled == true) {
                        var disabled = 0;
                    } else {
                        var disabled = 1;
                    }
                    let datas = {
                        user_name: this.formData.user_name,
                        password: this.formData.password,
                        mobile: this.formData.mobile,
                        real_name: this.formData.real_name,
                        department_id: this.formData.department_id,
                        main_account: main_account,
                        disabled: disabled,
                        node_ids: this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()).join(','),
                        hash: this.hash,
                    }
                    if (this.title == '新增') {
                        var urls = areaManageActAddApi;
                    } else if (this.title == '编辑') {
                        var urls = areaManageActEditApi;
                        datas.id = this.listId;

                    }
                    const res = await this.axios.post(urls, datas)
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: "success"
                        });
                        this.getList();
                        // this.$refs[formName].resetFields();//清空表单数据
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: "error"
                        });
                    }
                    this.getHash();
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        /* resetForm(formName) {
            this.$refs[formName].resetFields();
        }, */
        //获取节点列表
        async getNodeData(e) {
            this.nodeData = [];
            const res = await this.axios.post(areaManageViewApi)
            if (res.data.code == 1) {
                this.departmentData = res.data.data.department;
                this.regionData = res.data.data.region;
                this.nodeData = res.data.data.nodes;
                if (e && this.title == '编辑') {
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys([]);
                        e.forEach((i) => {
                            var node = this.$refs.tree.getNode(i);
                            if (node && node.isLeaf) {
                                this.$refs.tree.setChecked(node, true);
                            }
                        });
                    })
                } else if (this.title == '新增') {
                    // 获取所有id 赋值树节点
                    const arr = [];
                    this.nodeData.forEach(data => {
                        arr.push(data.id)
                        if (data.children) {
                            data.children.forEach(item => {
                                arr.push(item.id)
                            })
                        }
                    });
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys(arr);
                    })
                }

            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //获取账号信息
        async getInfoData(id) {
            this.infoData = [];
            const datas = {
                id: id
            };
            const res = await this.axios.post(areaManageInfoApi, datas)
            if (res.data.code == 1) {
                this.infoData = res.data.data;
                this.hasNodes = res.data.data.nodes;
                this.getNodeData(this.hasNodes);

                this.listId = res.data.data.id;
                this.formData.user_name = res.data.data.user_name;
                this.formData.real_name = res.data.data.real_name;
                this.formData.mobile = res.data.data.mobile;
                this.formData.department_id = res.data.data.department_id;
                this.formData.password = '';
                if (res.data.data.disabled == 1) {
                    this.formData.disabled = false;
                } else {
                    this.formData.disabled = true;
                }
                if (res.data.data.main_account == 1) {
                    this.formData.main_account = true;
                } else {
                    this.formData.main_account = false;
                }
                this.formData.selectVal1 = res.data.data.department_id;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //删除
        async delBtn(id) {
            this.$confirm("是否删除该条数据?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消"
            })
                .then(async () => {
                    const datas = {
                        id: id,
                        deleted: 1
                    };

                    const res = await this.axios.post(areaManageActDelApi, datas)
                    if (res.data.code == 1) {
                        if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                            this.curr -= 1;
                        }
                        this.$nextTick(() => {
                            this.getList();
                        });
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: "error"
                        });
                    }
                    this.getHash();
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        },

        // 点击新增
        addBtn() {
            this.title = '新增';
            this.dialogVisible = true;
            this.$nextTick(() => {
                this.resetForm();
            });
            this.getNodeData();//获取节点列表
            this.getHash();
        },

        // 点击编辑
        editBtn(e) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.$nextTick(() => {
                this.$refs['formData'].clearValidate();
            });
            this.getInfoData(e.id);
            this.getHash();
        },


        //重置对话框表单
        resetForm() {
            this.formData = {
                user_name: '',
                real_name: "",
                password: "",
                mobile: "",
                selectVal1: "",
                main_account: true,
                disabled: true
            };
            this.nodeData = [];
            this.$nextTick(() => {
                this.$refs['formData'].clearValidate();
            });
        },
        //关闭弹窗
        handleClose(formName) {
            this.dialogVisible = false;
        },
        //获取hash
        async getHash() {
            const datas = {
                page_hash: 'areaAccount'
            };
            const res = await this.axios.post(hashApi, datas)
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        // 切换表格状态开关
        async switchChange(id, key, val, idx) {
            let vals = val == true ? 1 : 0;
            if (key == 'disabled') {
                vals = val == false ? 1 : 0;
                var url = areaManageSetApi;
            }
            let datas = {
                id: id,
                state: vals
            };
            const res = await this.axios.post(url, datas)
            if (res.data.code == 1) {
                console.log('成功')
            } else {
                this.tableData[idx][key] = !val
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },

        //切换部门
        quvSelectChange(e) {
            this.formData.department_id = e;
        },
        //获取资源列表
        /* async getResource() {
            const res = await this.axios.post(areaManageViewApi)
            if (res.data.code == 1) {
                this.departmentData = res.data.data.department;
                this.regionData = res.data.data.region;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        }, */
        //获取区级账号列表
        async getList(e) {
            if (e) {
                this.curr = 1;
            }
            const datas = {
                region_id: this.region_id,
                keyword: this.keyword,
                curr: this.curr,
                pagesize: this.pageSize,
            };
            this.isLoading = true;
            const res = await this.axios.post(areaManageListApi, datas)
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
                let resources = res.data.data.resources;
                var obj = this.method_auth;
                this.isCity = res.data.data.resources.grade_auth.grade_status;
                this.main_auth = res.data.data.resources.main_auth.main_account;
                resources.method_auth.map(function (value) {
                    if (value.method_name) {
                        obj[value.method_name] = value.exist
                    }
                });
                this.method_auth = obj;
                this.isLoading = false;
            } else {
                this.isLoading = false;
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
    },
};
</script>
<style>
.dialogStyle .el-form-item {
    margin-bottom: 10px !important;
}

.dialogStyle .el-form-item__label,
.dialogStyle .el-form-item__content {
    line-height: 30px !important;
}
</style>
<style lang="scss" scoped>
.districtAccount {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .add {
            flex: 1;
            text-align: right;
        }
    }

    .dialogInput {
        .tree_main {
            border: 1px solid #dcdfe6;
            padding: 15px;
            max-height: 300px;
            overflow-y: auto;
        }
        .padd {
            width: 100%;
            margin-bottom: 10px;
            .paddTit {
                margin-bottom: 10px;
            }

            .paddTit::before {
                content: '*';
                color: #f56c6c;
                margin-right: 4px;
            }
        }
    }
    .btn {
        width: 300px;
        margin-top: 20px;
    }
}
</style>



